.services {
  padding: 3.125em 0;
  overflow: hidden;
  &__inner {
    display: flex;
  }
  &__left {
    flex-shrink: 0;
    width: 23.875em;
    margin-right: 2em;
    position: relative;
    z-index: 2;
  }
  &__right {
    width: calc(100% - 25.875em);
  }
  &__slider {
    position: relative;
  }
  &-swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  @media screen and (min-width: 769px) {
    .swiper-slide {
      opacity: 0;
      transition: all .3s;
      pointer-events: none;
      &-active,
      &-next,
      &-next,
      &-next + .swiper-slide {
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  .swiper-button-next {
    right: -1.875em;
  }
  .swiper-button-prev {
    left: -1.875em;
  }
  &__banner {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    background-color: #66E6EE;
    background-image: url("../img/banner-bg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    min-height: 7.875em;
    border-radius: 1.875em 0;
    padding: 1.25em 6.25em 1.25em 0.625em;
    overflow: hidden;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: -6.125em;
      right: -11.0625em;
      width: 15.625em;
      height: 6.9375em;
      background: #FFFBAB;
      filter: blur(6.375em);
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      left: -3.875em;
      bottom: -90%;
      width: 11.3125em;
      height: 9.5em;
      background: #0077FF;
      opacity: 0.4;
      filter: blur(4.375em);
      pointer-events: none;
    }
  }
  &__banner2 {
    background-color: #2ABA86;
    &:after {
      background: #00FFFF;
    }
    &:before {
      background: #24FF00;
    }
  }
  &__banner3 {
    background-color: #FFA585;
    &:after {
      background: #CCFF00;
    }
    &:before {
      background: #FFEDA0;
    }
  }
  &__banner4 {
    background-color: #F15F79;
    &:after {
      background: #FFFBAB;
    }
    &:before {
      background: #F41E23;
    }
  }
  &__title {
    min-height: 4.3125em;
    display: flex;
    align-items: center;
    span {
      font-size: 1.5em;
      line-height: 120%;
      font-weight: 700;
      color: #FFFFFF;
    }
  }
  &__icon {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 7.6875em;
    img {
      display: block;
      width: 100%;
    }
  }
  &__subtitle {
    margin-top: 2.1875em;
    span {
      font-size: 1.25em;
      line-height: 120%;
      color: #1D9CAC;
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.875em;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2.6875em;
    font-size: 1.125em;
    line-height: 120%;
    border-radius: 4.5em;
    padding: 0 0.625em;
    margin-bottom: 1.25em;
    &:not(:last-child) {
      margin-right: 1.25em;
    }
  }
  .green {
    color: #7BBA2A;
    border: 0.0625em solid #7BBA2A;
    &:hover {
      background: #7BBA2A;
      color: #ffffff;
    }
  }
  .orange {
    border: 0.0625em solid #E07746;
    border-radius: 4.5em;
    color: #E07746;
    &:hover {
      background: #E07746;
      color: #ffffff;
    }
  }
  .pink {
    border: 0.0625em solid #F41E23;
    border-radius: 4.5em;
    color: #F41E23;
    &:hover {
      background: #F41E23;
      color: #ffffff;
    }
  }
}
.services-card {
  height: auto;
  background: #FFFFFF;
  filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.07));
  border: 0.0625em solid transparent;
  border-radius: 0.625em;
  padding: 0.9375em;
  position: relative;
  &2 {
    border: 0.0625em solid #7BBA2A;
  }
  &:hover {
    filter: drop-shadow(0px 0px 14px rgba(123, 186, 42, 0.5));
  }
  &__info {
    position: absolute;
    z-index: 1;
    top: -0.75em;
    left: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-height: 2.625em;
    border-radius: 3.125em 0;
    padding: 0.3125em 0.9375em;
    span {
      font-size: 1em;
      line-height: 120%;
      font-weight: 700;
      color: #FFFFFF;
    }
  }
  .product-day {
    background: #0077FF;
  }
  .discount {
    background: #7BBA2A;
  }
  .hit {
    background: #F40000;
  }
  .new-products {
    background: #FFCC2F;
  }
  &__top {
    position: relative;
  }
  &__img {
    position: relative;
    padding-bottom: 83%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__like {
    position: absolute;
    top: 0.3125em;
    right: 0.3125em;
    padding: 0;
    margin: 0;
    background: transparent;
  }
  ._active {
    svg path {
      fill: #F40000;
      stroke: #F40000;
    }
  }
  &__analysis {
    position: absolute;
    top: 2.375em;
    right: 0.3125em;
    width: 1.875em;
    padding: 0;
    margin: 0;
    background: transparent;
    img {
      width: 100%;
    }
  }
  &__desc {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin-top: 0.4375em;
    &-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 1.25em;
    }
  }
  &__title {
    min-height: 6.375em;
    font-size: 1.125em;
    line-height: 120%;
    font-weight: 500;
    color: #2C2C2C;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  &__brand {
    display: flex;
    align-items: center;
    margin-top: 1.25em;
    span {
      font-size: 1.125em;
      line-height: 120%;
      color: #2C2C2C;
      opacity: 0.4;
    }
    span:nth-child(2) {
      margin-left: 1.25em;
    }
  }
  .hide {
    opacity: 0;
    pointer-events: none;
  }
  &__price {
    font-size: 1.75em;
    line-height: 120%;
    font-weight: 700;
    color: #7BBA2A;
    margin-right: 0.4375em;
  }
  &__bottom-btns {
    display: flex;
    align-items: center;
  }
  &__cartcalc {
    min-width: 6.25em;
    height: 34px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #F1F1F1;
    border-radius: 2em;
    padding: 0.3125em 0.25em;
    input {
      text-align: center;
      width: 1.5em;
      height: 1.5em;
      background: transparent;
      padding: 0;
      border: none;
      font-size: 1em;
      line-height: 120%;
      color: #2C2C2C;
      opacity: 0.5;
    }
  }
  &__ccalc-minus {
    width: 1.8em;
    height: 1.8em;
    background: #FFFFFF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
  }
  &__ccalc-plus {
    width: 1.8em;
    height: 1.8em;
    background: #FFFFFF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
  }
  &__basket {
    width: 34px;
    height: 34px;
    border-radius: 0.3125em;
    background: #7BBA2A;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0.75em;
    &:hover {
      background: darken(#7BBA2A, 10%);
    }
  }
}
