  }
  .news-card {
    padding: 0.9375em;
    &__date {
      min-width: 5.625em;
      span {
        font-size: 1.125em;
      }
    }
    &__text {
      margin-left: 1.5625em;
    }
    &__title {
      font-size: 1em;
    }
    &__icon {
      display: none;
    }
  }
  .reviews-card {
    &__img {
      display: none;
    }
  }
}