.display2 {
  font-size: 2em;
  line-height: 120%;
  font-weight: 700;
  color: #2C2C2C;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiper-button-next,
.swiper-button-prev {
  width: 2.5em;
  height: 2.5em;
  background: #FFFFFF;
  box-shadow: 0 0 1.25em rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  transition: all 0.5s;
  z-index: 2;
  svg {
    width: 0.5625em;
    height: 1.125em;
    transition: all 0.5s;
  }
  &:after {
    display: none;
  }
  &:hover {
    filter: drop-shadow(0px 0px 14px rgba(123, 186, 42, 0.5));
    svg path {
      stroke: #7BBA2A;
    }
  }
}
.swiper-button-disabled {
  opacity: 1 !important;
}