.footer {
  background: #F1F1F1;
}
.footer-top {
  padding: 3.125em 0 3.125em;
  &__inner {
    display: flex;
  }
  &__left {
    width: 12.3125em;
    margin-right: 3.75em;
  }
  &__right {
    width: calc(100% - 16.0625em);
  }
  &__logo {
    img {
      width: 100%;
      &:nth-child(2) {
        display: none;
      }
    }
  }
  &__payment {
    margin-top: 1em;
    &-title {
      font-size: 1em;
      line-height: 155%;
      color: #000000;
    }
    &-items {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1em / 2));
      gap: 1em;
      max-width: 9.375em;
      margin-top: 1em;
    }
    &-item {
      img {
        width: auto;
      }
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    &:not(:first-child) {
      margin-left: 2em;
    }
    &-flex {
      display: flex;
    }
  }
  &__nav {
    max-width: 12.3125em;
    &-title {
      font-size: 1.25em;
      line-height: 120%;
      font-weight: 500;
      color: #000000;
    }
    ul {
      margin-top: 1.25em;
      li {
        font-size: 1em;
        line-height: 155%;
        color: #000000;
        opacity: 0.5;
        &:not(:first-child) {
          margin-top: 0.75em;
        }
      }
      li a {
        &:hover {
          color: #7BBA2A;
        }
      }
    }
  }
  &__tel {
    font-weight: 700;
  }
  &__connection {
    margin-left: 3.125em;
  }
  &__btn {
    width: 10.0625em;
    height: 3.0625em;
    background: #7BBA2A;
    border-radius: 0.3125em;
    &:hover {
      background: darken(#7BBA2A, 10%);
    }
    span {
      font-size: 0.875em;
      line-height: 120%;
      font-weight: 500;
      color: #FFFFFF;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-top: 1.25em;
  }
  &__social {
    width: 1.875em;
    height: 1.875em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    &:not(:first-child) {
      margin-left: 0.75em;
    }
    img {
      max-width: 1.0625em;
      width: 100%;
      aspect-ratio: auto;
      -o-object-fit: contain;
      object-fit: contain;
    }
  }
  .ok {
    background: #F79A38;
  }
  .vk {
    background: #0077FF;
  }
  .telegram {
    background: #27A6E5;
  }
  .youtube {
    background: #F40000;
  }
}
.footer-bottom {
  padding: 1.25em 0;
  border-top: 0.0625em solid rgba(44, 44, 44, 0.2);
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__politics {
    font-size: 0.875em;
    line-height: 120%;
    color: #000000;
    opacity: 0.3;
  }
  &__brevis {
    display: flex;
    align-items: center;
    &-icon {
      width: 2.5625em;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-top: -0.625em;
      margin-left: 0.5em;
      p {
        font-size: 0.875em;
        line-height: 120%;
        color: #2C2C2C;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer-top {
    padding: 2em 0;
    &__left {
      width: 9.125em;
      margin-right: 3.125em;
    }
    &__right {
      width: calc(100% - 12.25em);
    }
    &__item-flex {
      flex-direction: column;
    }
    &__connection {
      margin: 1.25em 0 0 0;
    }
    &__nav {
      ul {
        li {
          font-size: 0.875em;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__left {
      width: 4.0625em;
    }
    &__right {
      width: calc(100% - 7.1875em);
    }
    &__logo {
      img {
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
    &__payment-items {
      grid-template-columns: 100%;
    }
    &__nav {
      &-title {
        font-size: 1em;
      }
    }
  }
  .footer-bottom {
    padding: 1em 0;
    &__brevis {
      &-desc {
        p a {
          display: block;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__items {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 3.125em / 2));
      gap: 3.125em;
    }
    &__item {
      &:not(:first-child) {
        margin: 0;
      }
    }
  }
  .footer-bottom {
    &__inner {
      display: block;
    }
    &__brevis {
      margin-top: 1.5625em;
    }
  }
}

@media screen and (max-width: 400px) {
  .footer-top {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 3.125em 0;
    }
    &__right {
      width: 100%;
    }
    &__logo {
      display: block;
      max-width: 3.75em;
    }
    &__payment {
      margin: 2em 0 0 0;
      &-items {
        grid-template-columns: repeat(3, calc(100% / 3 - 2em / 3));
        gap: 1em;
        max-width: 100%;
      }
    }
    &__items {
      grid-template-columns: 100%;
    }
  }
}